import React from "react";
import ReactDOM from "react-dom";
import style from "./Modal.module.css";

class Modal extends React.Component {
	render() {
		const {onClick, display, dataIndex, children} = this.props;

		const element = (
			<React.Fragment>
				<div className={style.modal_wrapper} onClick={onClick} data-index={dataIndex}>
					{children}
				</div>
			</React.Fragment>
		);

		const portalElement = ReactDOM.createPortal(element, document.body);

		return display ? portalElement : null;
	}
}

export default Modal;
