import React from "react";
import About from "./container/About";
import Home from "./container/Home";

export default class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      layer: 1
    };

    this.clickHandler = this.clickHandler.bind(this);
    console.log(
      "%c React using Facebook Incubator Create React App. ",
      "color: rgba(255,10,10,0.5)"
    );
    console.log(
      "%c Canvas has created from scratch, pure JS without plugin / external lib.",
      "color: rgba(255,10,10,0.5)"
    );
    console.log(
      "%c No mobile design for this one.",
      "color: rgba(255,10,10,0.5)"
    );
  }
  clickHandler(layer) {
    setTimeout(
      function() {
        this.setState({
          layer: layer
        });
      }.bind(this),
      2000
    );
  }
  render() {
    switch (this.state.layer) {
      case 1:
        return <Home clickHandler={this.clickHandler} />;
      case 2:
        return <About />;
      default:
        return <div>state layer not found, what are you doing?</div>;
    }
  }
}
